
import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve, Router, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';

import { ProgramSetupService } from './program-setup.service';
import { SharedService } from 'src/app/shared/services/shared.service';


@Injectable({
  providedIn: 'root'
})
export class ProgramSetupResolver implements Resolve<any> {

  constructor(
    private router: Router,
    private programSetupService: ProgramSetupService,
    private sharedService:SharedService
  ) { }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> | Promise<any> | any
  {

    let data = this.programSetupService.getProgramSetupMatDrawer();

    if(data== null){

      const parentUrl = state.url.split('//')[0].replace(/[{()}]/g, '')

      // Navigate to parent url
      this.router.navigateByUrl(parentUrl);

    }


    return;

  }


}

