import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ProgramSetupService {

  private programSetupMatDrawer = new BehaviorSubject<any>(null);
  programstupmatdrawercast = this.programSetupMatDrawer.asObservable();

  constructor() { }

  getProgramSetupMatDrawer(){
    return this.programSetupMatDrawer.value
  }

  setProgramSetupMatDrawer(value){
    this.programSetupMatDrawer.next(value);
  }

}
