import { Component, OnInit, ViewChild, ViewEncapsulation, ChangeDetectorRef } from '@angular/core';
import { MatDrawer } from '@angular/material/sidenav';

import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';

import { Subscription, Subject } from 'rxjs';
import { ProgramSetupService } from './program-setup.service';


@Component({
  selector: 'program-setup',
  templateUrl: './program-setup.component.html',
  styleUrls: ['./program-setup.component.scss'],
  encapsulation  : ViewEncapsulation.None
})
export class ProgramSetupComponent implements OnInit {

  navArray:any;

  @ViewChild('matDrawer', {static: true})
  matDrawer: MatDrawer;

  drawerMode: 'over';

  // Private
  private _unsubscribeAll: Subject<any>;

  constructor(
    private _changeDetectorRef: ChangeDetectorRef,
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
    private programSetupService: ProgramSetupService
  ) {

    // Set the private defaults
    this._unsubscribeAll = new Subject();

  }

  onBackdropClicked() {

    // Get the current activated route
    let route = this._activatedRoute;
    while ( route.firstChild )
    {
        route = route.firstChild;
    }

    // Go to the parent route
    this._router.navigate(['/program/setup'], {relativeTo: route});
    this.matDrawer.close();
    this.programSetupService.setProgramSetupMatDrawer(null)


    // Mark for check
    this._changeDetectorRef.markForCheck();

  }

  addMainProgram(id) {

    // Get the current activated route
    let route = this._activatedRoute;

    while ( route.firstChild )
    {
        route = route.firstChild;
    }

    this._router.navigate([ { outlets: { primary: 'main', drawer: ['new', id] } } ], { relativeTo: route.parent.parent })

    //Mark for check
    this._changeDetectorRef.markForCheck();

  }

  editMainProgram(id) {

    // Get the current activated route
    let route = this._activatedRoute;

    while ( route.firstChild )
    {
        route = route.firstChild;
    }

    this._router.navigate([ { outlets: { primary: 'main', drawer: ['edit', id] } } ], { relativeTo: route.parent.parent })

    //Mark for check
    this._changeDetectorRef.markForCheck();

  }

  viewMainProgram(id) {

    // Get the current activated route
    let route = this._activatedRoute;

    while ( route.firstChild )
    {
        route = route.firstChild;
    }

    this._router.navigate([ { outlets: { primary: 'main', drawer: 'view' } } ], { relativeTo: route.parent.parent })

    //Mark for check
    this._changeDetectorRef.markForCheck();

  }

  ngOnInit(): void {

    this.navArray = [
  		{link:'main', name: 'Main Program'},
  		{link:'class', name: 'Class Level'},
      {link:'style', name: 'Style'},
  		{link:'batch', name: 'Batch Type'},
  		{link:'room', name: 'Room'},
  		{link:'mode', name: 'Mode'}
  	]

    this.programSetupService.programstupmatdrawercast.subscribe((res:any) => {

      if(res != null) {
        if(res.id) {

          if(res.category == 'main'){
            if(!res.isedit){
              this.addMainProgram(res.id)
              if(res.data != null){
                this.viewMainProgram(res.id)
              }
            }
            else
              this.editMainProgram(res.id)
          }
        }


      }

    })

  }

}
