import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from 'src/app/core/auth/guards/auth.guard';

import { ProgramSetupComponent } from './program-setup.component';

import { AddMainProgramComponent } from 'src/app/modules/admin/program/program-setup/main-program/add-main-program/add-main-program.component';
import { ViewMainProgramComponent } from 'src/app/modules/admin/program/program-setup/main-program/view-main-program/view-main-program.component';

import { ProgramSetupResolver } from './program-setup.resolver';

const routes: Routes = [
  { path: 'list', component:  ProgramSetupComponent,
  children: [
    {
      path: ":type/:id",
      component: AddMainProgramComponent,
      outlet: "drawer",
      resolve: {
        ProgramSetupResolver
      }
    },
    {
      path: "view",
      component: ViewMainProgramComponent,
      outlet: "drawer",
      resolve: {
        ProgramSetupResolver
      }
    },
    { path: 'main', loadChildren: () => import('src/app/modules/admin/program/program-setup/main-program/main-program.module').then(m => m.MainProgramModule)},
    { path: '', redirectTo: 'main', pathMatch: 'full' }
  ]
  },
  { path: '', redirectTo: 'list', pathMatch: 'full' }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProgramSetupRoutingModule { }
