import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Subject } from 'rxjs';

import { ProgramSetupComponent } from 'src/app/modules/admin/program/program-setup/program-setup.component';

import { ProgramService } from 'src/app/api/controllers/Program';
import { ProgramSetupService } from 'src/app/modules/admin/program/program-setup/program-setup.service';
import { SharedService } from 'src/app/shared/services/shared.service';

import * as _ from 'underscore';
import * as moment from 'moment';
import _moment from "moment-timezone";


@Component({
  selector: 'app-add-main-program',
  templateUrl: './add-main-program.component.html',
  styleUrls: ['./add-main-program.component.scss']
})
export class AddMainProgramComponent implements OnInit {

  isProgramSubmitted:boolean = true;
  isEdit:boolean = false;

  program:any;

  private _unsubscribeAll: Subject<any>;

  constructor(
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
    private _changeDetectorRef: ChangeDetectorRef,
    private _programSetupComponent: ProgramSetupComponent,
    private programService:ProgramService,
    private programSetupService: ProgramSetupService,
    private sharedService:SharedService
  ) {

    // Set the private defaults
    this._unsubscribeAll = new Subject();

  }

  goBack() {
    this._programSetupComponent.matDrawer.close();
    this.programSetupService.setProgramSetupMatDrawer(null)
    this._router.navigate(['.'], {relativeTo: this._activatedRoute.parent});
  }

  submitMainProgramForm(form) {

    let details = {
      "id": 0,
      "topClientId": 1,
      "programTypeId": 1,
      "name": this.program.name,
      "description": this.program.description,
      "active": true,
      "lastModified": _moment().toISOString(),
      "modifiedBy": 0
    }

    this.programService.addUpdateProgram().subscribe((res:any) => {

    })

  }

  ngOnInit(): void {

    this.program = {

    }

    //Open the drawer
    this.programSetupService.programstupmatdrawercast.subscribe(res => {

      if(res != null) {
        if(res.id != undefined && res.id == this._activatedRoute.params['value'].id){
          this._programSetupComponent.matDrawer.open();
          if(this._activatedRoute.params['value'].type == 'edit'){
            this.isEdit = true
            this.program = res.data
          }
          else {
            this.isEdit = false
            this.program = {}
          }
        }
      }

      //Mark for check
      this._changeDetectorRef.markForCheck();

    })

  }

}
